/**axios
 * 
 */
import axios from 'axios';


/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params){
    return new Promise((resolve, reject) =>{
        axios.get(url, {
            params: params
        })
            .then(res => {
                resolve(res.data);
            })
            .catch(error => {
                reject(error.data);
            })
    });
}

/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, JSON.stringify(params))
            .then(res => {
                resolve(res.data);
            })
            .catch(error => {
                reject(error.data);
            })
    });
}

/**
 * putpost
 * @param {String} url [url]
 * @param {Object} params []
 */
